@import './../../style.scss';

.page-container{
    margin: auto 5px;
    .title {
        color: $fontColorStrong;
        font-size: 24px;
        margin-top: 30px;
        margin-bottom: 10px;
        text-align: center;
    }
    .seperator {
        width: 70px;
        height: 4px;
        background: gray;
        margin: auto;
    }

    table {
      font-size: 0.4em !important;
      width: 100% !important;
    }

    section {
      width: 100%;
      max-width: 100%;
      overflow: scroll;
    }

    table.ambox {
      margin: 0 1% !important;
    }

    @media only screen and (max-width: 374px) {
      width: 350px;
    }

    /* Extra small devices (phones, 600px and down) */
    @media only screen and (min-width: 375px) and (max-width: 400px) {
      width: 375px;
    }

    /* Extra small devices (phones, 600px and down) */
    @media only screen and (min-width: 401px) and (max-width: 425px) {
      width: 400px;
    }
  
    /* Extra small devices (phones, 600px and down) */
    @media only screen and (min-width: 426px) and (max-width: 450px) {
      width: 425px;
    }

    /* Extra small devices (phones, 600px and down) */
    @media only screen and (min-width: 450px) and (max-width: 600px) {
      width: 500px;
    }

    /* Small devices (portrait tablets and large phones, 600px and up) */
    @media only screen and (min-width: 600px) {
      width: 650px;
    }

    /* Medium devices (landscape tablets, 768px and up) */
    @media only screen and (min-width: 768px) {
      width: 900px;
    }
}

.minimap-treeview {
  background-color: #F8F8F8;
  padding: 5px 50px 5px 20px;
}

.hideclass {
  display: none !important; 
}
.showclass {
  display:block !important;
}

#coordinates {
  display: none;
}

.search-query {
  background-color: #8E8E931F;
  border-radius: 10px;
  opacity: 100%;
  padding: 5px 5px 5px 10px;
  color: #000000;
  outline: none;
  width: 250px;
  border: none;
  font-size: 14px;
  margin: 16px 0px 15px 23px;
}

.search-button {
  border-radius: 5px;
  padding: 3px;
  outline: none;
  cursor: pointer;
}

.page-section hr {
  margin-bottom: 35px;
}
