.slider { 
    position: fixed;
    opacity: 1;
    top: 10px;
    right: 15px;
    //width: 15%;
    min-width: 20px;
    max-width: 60px;
    //border: solid 1px #000;
    box-shadow: 0 2px 13px rgba(0,0,0,0.3);
    cursor: grab;
    //opacity: 0.5;
    transition: opacity 800ms ease-in-out 200ms;
    z-index: 999;
    z-index: 100000;
    background-color: aliceblue;
    // &:hover {
    //   opacity: 1;
    //   transition-delay: 0ms;
    // }
  }
  
  .slider__size {
    position: relative;
    z-index: 3;
  }
  
  .slider__controller {
  //  outline: solid 3px black;
    width: 100%;
    padding-top: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform-origin: 0 0;
    margin: -3px;
    border-radius: 10px;
    border: solid 3px black;
  }
  
  .slider__content { 
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transform-origin: 0 0;
  }
  
  .minimap {
//     z-index : 10000;
//     cursor: pointer;
//     font-size: 1em;
// /*    white-space: nowrap;*/
//     position: fixed;
//     transform-origin: 0px 0px;

    z-index: 10000;
    cursor: pointer;
    font-size: 1em;
    /* white-space: nowrap; */
    position: fixed;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
 
    -webkit-perspective: 1000;
    -moz-perspective: 1000;
    -ms-perspective: 1000;
    perspective: 1000;
    -webkit-transform:scale(0.0607, 0.01063);
    -moz-transform:scale(0.0607, 0.01063);
    transform:scale(0.0607, 0.01063); 
    -webkit-font-smoothing: subpixel-antialiased;
    width: 256.25px;
    margin: 0px;
    padding: 0px;
    transform-origin: 0px 0px;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
