@import './../../style.scss';

.container {
    box-shadow: 0px 3px 6px $borderGray;
    text-align: center;
    padding: 15px 20px;
    img {
        cursor: pointer;
        width: 36px;
        height: 36px;
    }
    .menu-bar {
        float: left;
    }
    .user-profile {
        float: right;
    }
    .icon-brand {
        color:$boxShadowBlack;
        font-size: 23px;
        margin-top: 4px;
        font-family: serif;
    }
}
